<template><section><h1>信息窗口</h1>
<hr>
<h2>示例</h2>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"map-present"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">id</span>=<span class="hljs-string">"demoComponent"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-component"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">amap</span> <span class="hljs-attr">vid</span>=<span class="hljs-string">"amap"</span> <span class="hljs-attr">:zoom</span>=<span class="hljs-string">"zoom"</span> <span class="hljs-attr">:center</span>=<span class="hljs-string">"center"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">amap-info-window</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"window in windows"</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"window.position"</span> <span class="hljs-attr">:content</span>=<span class="hljs-string">"window.content"</span> <span class="hljs-attr">:open</span>=<span class="hljs-string">"window.open"</span> <span class="hljs-attr">:events</span>=<span class="hljs-string">"window.events"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">amap-info-window</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">amap</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
</code></pre>
<pre><code class="language-javascript"><span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">name</span>: <span class="hljs-string">'demoComponent'</span>,
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">zoom</span>: <span class="hljs-number">14</span>,
      <span class="hljs-attr">center</span>: [<span class="hljs-number">121.5273285</span>, <span class="hljs-number">31.21515044</span>],
      <span class="hljs-attr">windows</span>: [
        {
          <span class="hljs-attr">position</span>: [<span class="hljs-number">121.5273285</span>, <span class="hljs-number">31.21515044</span>],
          <span class="hljs-attr">content</span>: <span class="hljs-string">'Hi! I am here!'</span>,
          <span class="hljs-attr">open</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">events</span>: {
            close() {
              <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'close infowindow'</span>);
            }
          }
        }
      ]
    }
  }
}
