<p><demo></demo></p>

<h2>静态属性</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>isCustom</td>
<td>Boolean</td>
<td>是否自定义窗体。设为true时，信息窗体外框及内容完全按照content所设的值添加（默认为false，即在系统默认的信息窗体外框中显示content内容）</td>
</tr>
<tr>
<td>autoMove</td>
<td>Boolean</td>
<td>是否自动调整窗体到视野内（当信息窗体超出视野范围时，通过该属性设置是否自动平移地图，使信息窗体完全显示）</td>
</tr>
<tr>
<td>closeWhenClickMap</td>
<td>Boolean</td>
<td>控制是否在鼠标点击地图后关闭信息窗体，默认false，鼠标点击地图后不关闭信息窗体</td>
</tr>
<tr>
<td>showShadow</td>
<td>Boolean</td>
<td>Boolean 控制是否显示信息窗体阴影，取值false时不显示窗体阴影，取值true时显示窗体阴影默认值：false</td>
</tr>
<tr>
<td>offset</td>
<td>Array</td>
<td>相对于基点的偏移量。默认情况是信息窗体的底部中心点(BOTTOM_CENTER) 和基点之间的偏移量</td>
</tr>
</tbody>
</table>
<h2>动态属性</h2>
<p>支持响应式。</p>
<table>
<thead>
<tr>
<th>名称</th>
<th>类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>content</td>
<td>String/HTML</td>
<td>显示内容。支持字符串和HTML。</td>
</tr>
<tr>
<td>size</td>
<td>Size</td>
<td>信息窗体尺寸（isCustom为true时，该属性无效）</td>
</tr>
<tr>
<td>position</td>
<td>Array</td>
<td>信息窗体显示基点位置（自v1.2 新增）</td>
</tr>
</tbody>
</table>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件</th>
<th>参数</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td></td>
<td>属性发生变化时</td>
</tr>
<tr>
<td>open</td>
<td></td>
<td>信息窗体打开之后触发事件</td>
</tr>
<tr>
<td>close</td>
<td></td>
<td>信息窗体关闭之后触发事件</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import Demo from 'demos/info-window.vue';
export default {
  components: {
    Demo
  }
}
</script>


