<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">zoom</span>: <span class="hljs-number">14</span>,
      <span class="hljs-attr">center</span>: [<span class="hljs-number">121.5273285</span>, <span class="hljs-number">31.21515044</span>],
      <span class="hljs-attr">markers</span>: [
        {
          <span class="hljs-attr">position</span>: [<span class="hljs-number">121.5273285</span>, <span class="hljs-number">31.21515044</span>],
          <span class="hljs-attr">events</span>: {
            <span class="hljs-attr">click</span>: <span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
              alert(<span class="hljs-string">'click marker'</span>);
              <span class="hljs-built_in">console</span>.log(<span class="hljs-keyword">this</span>);
            },
            <span class="hljs-attr">dragend</span>: <span class="hljs-function">(<span class="hljs-params">e</span>) =&gt;</span> {
              <span class="hljs-built_in">console</span>.log(e);
              <span class="hljs-keyword">this</span>.markers[<span class="hljs-number">0</span>].position = [e.lnglat.lng, e.lnglat.lat];
            }
          },
          <span class="hljs-attr">visible</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">draggable</span>: <span class="hljs-literal">false</span>
        }
      ]
    };
  },
  <span class="hljs-attr">methods</span>: {
    changePosition() {
      <span class="hljs-keyword">let</span> position = <span class="hljs-keyword">this</span>.markers[<span class="hljs-number">0</span>].position;
      <span class="hljs-keyword">this</span>.markers[<span class="hljs-number">0</span>].position = [position[<span class="hljs-number">0</span>] + <span class="hljs-number">0.002</span>, position[<span class="hljs-number">1</span>] - <span class="hljs-number">0.002</span>];
    },
    chnageDraggle() {
      <span class="hljs-keyword">let</span> draggable = <span class="hljs-keyword">this</span>.markers[<span class="hljs-number">0</span>].draggable;
      <span class="hljs-keyword">this</span>.markers[<span class="hljs-number">0</span>].draggable = !draggable;
    },
    toggleVisible() {
      <span class="hljs-keyword">let</span> visableVar = <span class="hljs-keyword">this</span>.markers[<span class="hljs-number">0</span>].visible;
      <span class="hljs-keyword">this</span>.markers[<span class="hljs-number">0</span>].visible = !visableVar;
    },
    addMarker() {
      <span class="hljs-keyword">let</span> marker = {
        <span class="hljs-attr">position</span>: [<span class="hljs-number">121.5273285</span> + (<span class="hljs-built_in">Math</span>.random() - <span class="hljs-number">0.5</span>) * <span class="hljs-number">0.02</span>, <span class="hljs-number">31.21515044</span> + (<span class="hljs-built_in">Math</span>.random() - <span class="hljs-number">0.5</span>) * <span class="hljs-number">0.02</span>]
      };
      <span class="hljs-keyword">this</span>.markers.push(marker);
    },
    removeMarker() {
      <span class="hljs-keyword">if</span> (!<span class="hljs-keyword">this</span>.markers.length) <span class="hljs-keyword">return</span>;
      <span class="hljs-keyword">this</span>.markers.splice(<span class="hljs-keyword">this</span>.markers.length - <span class="hljs-number">1</span>, <span class="hljs-number">1</span>);
    }
  }
};
