<h2>静态属性</h2>
<p>仅且可以初始化配置，不支持响应式。</p>
<table>
<thead>
<tr>
<th>名称</th>
<th>类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>vid</td>
<td>String</td>
<td>marker对象id</td>
</tr>
<tr>
<td>topWhenClick</td>
<td>Boolean</td>
<td>鼠标点击时marker是否置顶，默认false ，不置顶（自v1.3 新增）</td>
</tr>
<tr>
<td>bubble</td>
<td>Boolean</td>
<td>是否将覆盖物的鼠标或touch等事件冒泡到地图上（自v1.3 新增）默认值：false</td>
</tr>
<tr>
<td>autoRotation</td>
<td>Boolean</td>
<td>是否自动旋转。点标记在使用moveAlong动画时，路径方向若有变化，点标记是否自动调整角度，默认为falseIE8以下不支持旋转，autoRotation属性无效</td>
</tr>
</tbody>
</table>
<h2>动态属性</h2>
<p>支持响应式。</p>
<table>
<thead>
<tr>
<th>名称</th>
<th>类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>visible</td>
<td>Boolean</td>
<td>点标记是否可见，默认为true</td>
</tr>
<tr>
<td>zIndex</td>
<td>Number</td>
<td>点标记的叠加顺序。地图上存在多个点标记叠加时，通过该属性使级别较高的点标记在上层显示默认zIndex：100</td>
</tr>
<tr>
<td>position</td>
<td>Array</td>
<td>点标记在地图上显示的位置，默认为地图中心点</td>
</tr>
<tr>
<td>offset</td>
<td>Array</td>
<td>点标记显示位置偏移量，默认值为Pixel(-10,-34)。Marker指定position后，默认以marker左上角位置为基准点，对准所给定的position位置，若需使marker指定位置对准在position处，需根据marker的尺寸设置一定的偏移量。</td>
</tr>
<tr>
<td>icon</td>
<td>String</td>
<td>需在点标记中显示的图标。可以是一个本地图标地址，或者Icon对象。有合法的content内容时，此属性无效</td>
</tr>
<tr>
<td>content</td>
<td>String</td>
<td>点标记显示内容，可以是HTML要素字符串或者HTML DOM对象。content有效时，icon属性将被覆盖</td>
</tr>
<tr>
<td>draggable</td>
<td>Boolean</td>
<td>设置点标记是否可拖拽移动，默认为false</td>
</tr>
<tr>
<td>raiseOnDrag</td>
<td>Boolean</td>
<td>设置拖拽点标记时是否开启点标记离开地图的效果</td>
</tr>
<tr>
<td>cursor</td>
<td>String</td>
<td>指定鼠标悬停时的鼠标样式，自定义cursor，IE仅支持cur/ani/ico格式，Opera不支持自定义cursor</td>
</tr>
<tr>
<td>angle</td>
<td>Number</td>
<td>点标记的旋转角度注：angle属性是使用CSS3来实现的，支持IE9及以上版本</td>
</tr>
<tr>
<td>animation</td>
<td>String</td>
<td>点标记的动画效果，默认值：“AMAP_ANIMATION_NONE”可选值：“AMAP_ANIMATION_NONE”，无动画效果“AMAP_ANIMATION_DROP”，点标掉落果“AMAP_ANIMATION_BOUNCE”，点标弹跳效果</td>
</tr>
<tr>
<td>title</td>
<td>String</td>
<td>鼠标滑过点标记时的文字提示，不设置则鼠标滑过点标无文字提示</td>
</tr>
<tr>
<td>clickable</td>
<td>Boolean</td>
<td>点标记是否可点击</td>
</tr>
<tr>
<td>label</td>
<td>{content,offset}</td>
<td>添加文本标注，content为文本标注的内容，offset为偏移量，左上角为偏移量为（0,0）</td>
</tr>
</tbody>
</table>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件</th>
<th>参数</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>click</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标左键单击事件</td>
</tr>
<tr>
<td>dblclick</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标左键双击事件</td>
</tr>
<tr>
<td>rightclick</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标右键单击事件</td>
</tr>
<tr>
<td>mousemove</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标移动</td>
</tr>
<tr>
<td>mouseover</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标移近点标记时触发事件</td>
</tr>
<tr>
<td>mouseout</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标移出点标记时触发事件</td>
</tr>
<tr>
<td>mousedown</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标在点标记上按下时触发事件</td>
</tr>
<tr>
<td>mouseup</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标在点标记上按下后抬起时触发事件</td>
</tr>
<tr>
<td>dragstart</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>开始拖拽点标记时触发事件</td>
</tr>
<tr>
<td>dragging</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标拖拽移动点标记时触发事件</td>
</tr>
<tr>
<td>dragend</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>点标记拖拽移动结束触发事件</td>
</tr>
<tr>
<td>moveend</td>
<td></td>
<td>点标记执行moveTo动画结束时触发事件，也可以由moveAlong方法触发</td>
</tr>
<tr>
<td>movealong</td>
<td></td>
<td>点标记执行moveAlong动画一次后触发事件</td>
</tr>
<tr>
<td>touchstart</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>触摸开始时触发事件，仅适用移动设备</td>
</tr>
<tr>
<td>touchmove</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>触摸移动进行中时触发事件，仅适用移动设备</td>
</tr>
<tr>
<td>touchend</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>触摸结束时触发事件，仅适用移动设备</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import Demo from 'demos/marker.vue';
export default {
  components: {
    Demo
  }
}
</script>


