<h2>静态属性</h2>
<p>仅且可以初始化配置，不支持响应式。</p>
<table>
<thead>
<tr>
<th>名称</th>
<th>类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>bubble</td>
<td>Boolean</td>
<td>是否将覆盖物的鼠标或touch等事件冒泡到地图上（自v1.3 新增）默认值：false</td>
</tr>
</tbody>
</table>
<h2>动态属性</h2>
<p>支持响应式。</p>
<table>
<thead>
<tr>
<th>名称</th>
<th>类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>visible</td>
<td>Boolean</td>
<td>是否可见</td>
</tr>
<tr>
<td>editable</td>
<td>Boolean</td>
<td>当前圆形是否可编辑</td>
</tr>
<tr>
<td>center</td>
<td>LngLat</td>
<td>圆心位置</td>
</tr>
<tr>
<td>radius</td>
<td>Number</td>
<td>圆半径，单位:米</td>
</tr>
<tr>
<td>zIndex</td>
<td>Number</td>
<td>层叠顺序默认zIndex:10</td>
</tr>
<tr>
<td>strokeColor</td>
<td>String</td>
<td>线条颜色，使用16进制颜色代码赋值。默认值为#006600</td>
</tr>
<tr>
<td>strokeOpacity</td>
<td>Float</td>
<td>轮廓线透明度，取值范围[0,1]，0表示完全透明，1表示不透明。默认为0.9</td>
</tr>
<tr>
<td>strokeWeight</td>
<td>Number</td>
<td>轮廓线宽度</td>
</tr>
<tr>
<td>fillColor</td>
<td>String</td>
<td>圆形填充颜色,使用16进制颜色代码赋值。默认值为#006600</td>
</tr>
<tr>
<td>fillOpacity</td>
<td>Float</td>
<td>圆形填充透明度，取值范围[0,1]，0表示完全透明，1表示不透明。默认为0.9</td>
</tr>
<tr>
<td>strokeStyle</td>
<td>String</td>
<td>轮廓线样式，实线:solid，虚线:dashed</td>
</tr>
<tr>
<td>extData</td>
<td>Any</td>
<td>用户自定义属性，支持JavaScript API任意数据类型，如Circle的id等</td>
</tr>
</tbody>
</table>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件</th>
<th>参数</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>click</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标左键单击事件</td>
</tr>
<tr>
<td>dblclick</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标左键双击事件</td>
</tr>
<tr>
<td>rightclick</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>右键单击</td>
</tr>
<tr>
<td>hide</td>
<td>{type, target}</td>
<td>隐藏</td>
</tr>
<tr>
<td>show</td>
<td>{type, target}</td>
<td>显示</td>
</tr>
<tr>
<td>mousedown</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标按下</td>
</tr>
<tr>
<td>mouseup</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标抬起</td>
</tr>
<tr>
<td>mouseover</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标经过</td>
</tr>
<tr>
<td>mouseout</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标移出</td>
</tr>
<tr>
<td>change</td>
<td>{type, target}</td>
<td>属性发生变化时</td>
</tr>
<tr>
<td>touchstart</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>触摸开始时触发事件，仅适用移动设备</td>
</tr>
<tr>
<td>touchmove</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>触摸移动进行中时触发事件，仅适用移动设备</td>
</tr>
<tr>
<td>touchend</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>触摸结束时触发事件，仅适用移动设备</td>
</tr>
<tr>
<td>move</td>
<td>{type, target, lnglat}</td>
<td>编辑状态下， 拖拽圆心调整圆形位置时触发此事件type: 事件类型 target: 发生事件的目标对象 lnglat: 调整后圆的圆心坐标</td>
</tr>
<tr>
<td>adjust</td>
<td>{type, target, radius}</td>
<td>编辑状态下，鼠标调整圆形半径时，触发此事件 type: 事件类型  target: 发生事件的目标对象 radius: 调整后圆的半径，单位：米</td>
</tr>
<tr>
<td>end</td>
<td>{type,target}</td>
<td>关闭编辑状态时，该方法后触发，target即为编辑后的圆对象</td>
</tr>
</tbody>
</table>
</section></template>

