<template><section><h1>多边形</h1>
<hr>
<h2>示例</h2>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">id</span>=<span class="hljs-string">"demoComponent"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-component"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">amap</span> <span class="hljs-attr">vid</span>=<span class="hljs-string">"amap"</span> <span class="hljs-attr">:zoom</span>=<span class="hljs-string">"zoom"</span> <span class="hljs-attr">:center</span>=<span class="hljs-string">"center"</span>&gt;</span>
