        {
          <span class="hljs-attr">path</span>: [[<span class="hljs-number">121.5273285</span>, <span class="hljs-number">31.21515044</span>], [<span class="hljs-number">121.5293285</span>, <span class="hljs-number">31.21515044</span>], [<span class="hljs-number">121.5293285</span>, <span class="hljs-number">31.21915044</span>], [<span class="hljs-number">121.5273285</span>, <span class="hljs-number">31.21515044</span>]],
