<p><demo></demo></p>

<h2>静态属性</h2>
<p>仅且可以初始化配置，不支持响应式。</p>
<table>
<thead>
<tr>
<th>名称</th>
<th>类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>bubble</td>
<td>Boolean</td>
<td>是否将覆盖物的鼠标或touch等事件冒泡到地图上（自v1.3 新增）默认值：false</td>
</tr>
</tbody>
</table>
<h2>动态属性</h2>
<p>支持响应式。</p>
<table>
<thead>
<tr>
<th>名称</th>
<th>类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>path</td>
<td>Array</td>
<td>多边形轮廓线的节点坐标数组，当为“环”多边形时（多边形区域在多边形内显示为“岛”），path为二维数组，数组元素为多边形轮廓的节点坐标数组“环”多边形时，要求数组第一个元素为外多边形，其余为“岛”多边形，外多边形需包含“岛”多边形，否则程序不作处理</td>
</tr>
<tr>
<td>visible</td>
<td>Boolean</td>
<td>是否可见</td>
</tr>
<tr>
<td>editable</td>
<td>Boolean</td>
<td>多边形当前是否可编辑</td>
</tr>
<tr>
<td>zIndex</td>
<td>Number</td>
<td>多边形覆盖物的叠加顺序。地图上存在多个多边形覆盖物叠加时，通过该属性使级别较高的多边形覆盖物在上层显示默认zIndex：10</td>
</tr>
<tr>
<td>strokeColor</td>
<td>String</td>
<td>线条颜色，使用16进制颜色代码赋值。默认值为#006600</td>
</tr>
<tr>
<td>strokeOpacity</td>
<td>float</td>
<td>轮廓线透明度，取值范围[0,1]，0表示完全透明，1表示不透明。默认为0.9</td>
</tr>
<tr>
<td>strokeWeight</td>
<td>Number</td>
<td>轮廓线宽度</td>
</tr>
<tr>
<td>fillColor</td>
<td>String</td>
<td>多边形填充颜色，使用16进制颜色代码赋值，如：#FFAA00</td>
</tr>
<tr>
<td>fillOpacity</td>
<td>Float</td>
<td>多边形填充透明度，取值范围[0,1]，0表示完全透明，1表示不透明。默认为0.9</td>
</tr>
<tr>
<td>extData</td>
<td>Any</td>
<td>用户自定义属性，支持JavaScript API任意数据类型，如Polygon的id等</td>
</tr>
<tr>
<td>strokeStyle</td>
<td>String</td>
<td>轮廓线样式，实线:solid，虚线:dashed</td>
</tr>
</tbody>
</table>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件</th>
<th>参数</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>click</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标左键单击事件</td>
</tr>
<tr>
<td>dblclick</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标左键双击事件</td>
</tr>
<tr>
<td>rightclick</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>右键单击</td>
</tr>
<tr>
<td>hide</td>
<td>{type, target}</td>
<td>隐藏</td>
</tr>
<tr>
<td>show</td>
<td>{type, target}</td>
<td>显示</td>
</tr>
<tr>
<td>mousedown</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标按下</td>
</tr>
<tr>
<td>mouseup</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标抬起</td>
</tr>
<tr>
<td>mouseover</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标经过</td>
</tr>
<tr>
<td>mouseout</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标移出</td>
</tr>
<tr>
<td>change</td>
<td></td>
<td>属性发生变化时</td>
</tr>
<tr>
<td>touchstart</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>触摸开始时触发事件，仅适用移动设备</td>
</tr>
<tr>
<td>touchmove</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>触摸移动进行中时触发事件，仅适用移动设备</td>
</tr>
<tr>
<td>touchend</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>触摸结束时触发事件，仅适用移动设备</td>
</tr>
<tr>
<td>addnode</td>
<td><a href="(http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent)">MapsEvent</a></td>
<td>编辑状态下，通过鼠标在折线上增加一个节点或在多边形上增加一个顶点时触发此事件</td>
</tr>
<tr>
<td>adjust</td>
<td><a href="(http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent)">MapsEvent</a></td>
<td>编辑状态下，鼠标调整折线上某个节点或多边形上某个顶点的位置时触发此事件</td>
</tr>
<tr>
<td>removenode</td>
<td><a href="(http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent)">MapsEvent</a></td>
<td>编辑状态下，通过鼠标在折线上删除一个节点或在多边形上删除一个顶点时触发此事件</td>
</tr>
<tr>
<td>end</td>
<td>{type,target}</td>
<td>关闭编辑状态，触发该事件，target即为编辑后的折线/多边形对象</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import Demo from 'demos/polygon.vue';
export default {
  components: {
    Demo
  }
}
</script>


