<template><section><h1>地图</h1>
<hr>
<h2>示例</h2>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"amap-page-container"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-amap</span> <span class="hljs-attr">:vid</span>=<span class="hljs-string">"'amap-vue'"</span> <span class="hljs-attr">:center</span>=<span class="hljs-string">"center"</span> <span class="hljs-attr">:zoom</span>=<span class="hljs-string">"zoom"</span> <span class="hljs-attr">:map-manager</span>=<span class="hljs-string">"amapManager"</span> <span class="hljs-attr">:plugin</span>=<span class="hljs-string">"plugin"</span> <span class="hljs-attr">:events</span>=<span class="hljs-string">"events"</span>&gt;</span>
       <span class="hljs-tag">&lt;<span class="hljs-name">el-amap-marker</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"marker in markers"</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"marker"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-amap-marker</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">el-amap</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">v-on:click</span>=<span class="hljs-string">"getMap"</span>&gt;</span>get map<span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">v-on:click</span>=<span class="hljs-string">"addZoom"</span>&gt;</span>zoom++<span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">v-on:click</span>=<span class="hljs-string">"subZoom"</span>&gt;</span>zoom--<span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">v-on:click</span>=<span class="hljs-string">"changeCenter"</span>&gt;</span>change center<span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">import</span> { AMapManager } <span class="hljs-keyword">from</span> <span class="hljs-string">'vue-amap'</span>;
  <span class="hljs-keyword">let</span> amapManager = <span class="hljs-keyword">new</span> AMapManager();
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">name</span>: <span class="hljs-string">'amap-page'</span>,
    <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">vid</span>: <span class="hljs-string">'amap-vue-1'</span>,
        <span class="hljs-attr">zoom</span>: <span class="hljs-number">12</span>,
        <span class="hljs-attr">center</span>: [<span class="hljs-number">121.59996</span>, <span class="hljs-number">31.197646</span>],
        <span class="hljs-attr">events</span>: {
          <span class="hljs-string">'moveend'</span>: <span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
            <span class="hljs-keyword">let</span> mapCenter = <span class="hljs-keyword">this</span>.amapManager.getMap().getCenter();
            <span class="hljs-keyword">this</span>.center = [mapCenter.getLng(), mapCenter.getLat()];
          },
          <span class="hljs-string">'zoomchange'</span>: <span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
            <span class="hljs-keyword">this</span>.zoom = <span class="hljs-keyword">this</span>.amapManager.getMap().getZoom();
          },
          <span class="hljs-string">'click'</span>: <span class="hljs-function">(<span class="hljs-params">e</span>) =&gt;</span> {
            alert(<span class="hljs-string">'map clicked'</span>);
          }
        },
        <span class="hljs-attr">plugin</span>: [<span class="hljs-string">'ToolBar'</span>],
        <span class="hljs-attr">amapManager</span>: amapManager,
        <span class="hljs-attr">markers</span>: [
                 [<span class="hljs-number">121.59996</span>, <span class="hljs-number">31.197646</span>],
                 [<span class="hljs-number">121.40018</span>, <span class="hljs-number">31.197622</span>],
                 [<span class="hljs-number">121.69991</span>, <span class="hljs-number">31.207649</span>]]
      };
    },
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-attr">getMap</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
        <span class="hljs-built_in">console</span>.log(<span class="hljs-keyword">this</span>.amapManager.getMap());
      },
      addZoom() {
        <span class="hljs-keyword">this</span>.zoom++;
      },
      subZoom() {
        <span class="hljs-keyword">this</span>.zoom--;
      },
      changeCenter() {
        <span class="hljs-keyword">this</span>.center = [<span class="hljs-keyword">this</span>.center[<span class="hljs-number">0</span>] + <span class="hljs-number">0.1</span>, <span class="hljs-keyword">this</span>.center[<span class="hljs-number">1</span>] + <span class="hljs-number">0.1</span>];
        <span class="hljs-built_in">console</span>.log(<span class="hljs-keyword">this</span>.center);
      }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
