<p><demo></demo></p>

<h2>静态属性</h2>
<p>仅且可以初始化配置，不支持响应式。</p>
<table>
<thead>
<tr>
<th>名称</th>
<th>类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>vid</td>
<td>String</td>
<td>地图容器节点的ID</td>
</tr>
<tr>
<td>amapManager</td>
<td>AMapManager</td>
<td>地图管理对象</td>
</tr>
<tr>
<td>cursor</td>
<td>String</td>
<td>地图默认鼠标样式。参数cursor应符合CSS的cursor属性规范</td>
</tr>
<tr>
<td>animateEnable</td>
<td>Boolean</td>
<td>地图平移过程中是否使用动画（如调用panBy、panTo、setCenter、setZoomAndCenter等函数，将对地图产生平移操作，是否使用动画平移的效果），默认为true，即使用动画</td>
</tr>
<tr>
<td>isHotspot</td>
<td>Boolean</td>
<td>是否开启地图热点，默认false 不打开</td>
</tr>
<tr>
<td>rotateEnable</td>
<td>Boolean</td>
<td>地图是否可旋转，默认false</td>
</tr>
<tr>
<td>resizeEnable</td>
<td>Boolean</td>
<td>是否监控地图容器尺寸变化，默认值为false</td>
</tr>
<tr>
<td>showIndoorMap</td>
<td>Boolean</td>
<td>是否在有矢量底图的时候自动展示室内地图，PC端默认是true，移动端默认是false</td>
</tr>
<tr>
<td>expandZoomRange</td>
<td>Boolean</td>
<td>是否支持可以扩展最大缩放级别.设置为true的时候，zooms的最大级别在PC上可以扩大到20级，移动端还是高清19/非高清20</td>
</tr>
<tr>
<td>dragEnable</td>
<td>Boolean</td>
<td>地图是否可通过鼠标拖拽平移，默认为true。此属性可被setStatus/getStatus 方法控制</td>
</tr>
<tr>
<td>zoomEnable</td>
<td>Boolean</td>
<td>地图是否可缩放，默认值为true。此属性可被setStatus/getStatus 方法控制</td>
</tr>
<tr>
<td>doubleClickZoom</td>
<td>Boolean</td>
<td>地图是否可通过双击鼠标放大地图，默认为true。此属性可被setStatus/getStatus 方法控制</td>
</tr>
<tr>
<td>keyboardEnable</td>
<td>Boolean</td>
<td>地图是否可通过键盘控制，方向键控制地图平移，&quot;+&quot;和&quot;-&quot;可以控制地图的缩放，Ctrl+“→”顺时针旋转，Ctrl+“←”逆时针旋转，默认为true。此属性可被setStatus/getStatus 方法控制</td>
</tr>
<tr>
<td>jogEnable</td>
<td>Boolean</td>
<td>地图是否使用缓动效果，默认值为true。此属性可被setStatus/getStatus 方法控制</td>
</tr>
<tr>
<td>scrollWheel</td>
<td>Boolean</td>
<td>地图是否可通过鼠标滚轮缩放浏览，默认为true。此属性可被setStatus/getStatus 方法控制</td>
</tr>
<tr>
<td>touchZoom</td>
<td>Boolean</td>
<td>地图在移动终端上是否可通过多点触控缩放浏览地图，默认为true</td>
</tr>
</tbody>
</table>
<h2>动态属性</h2>
<p>支持响应式。</p>
<table>
<thead>
<tr>
<th>名称</th>
<th>类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>zooms</td>
<td>Array</td>
<td>地图显示的缩放级别范围，在PC上，默认范围[3,18]，取值范围[3-18]；在移动设备上，默认范围[3-19]，取值范围[3-19]</td>
</tr>
<tr>
<td>center</td>
<td>Array</td>
<td>地图中心点坐标值</td>
</tr>
<tr>
<td>labelzIndex</td>
<td>Number</td>
<td>地图标注显示顺序</td>
</tr>
<tr>
<td>lang</td>
<td>String</td>
<td>地图语言类型 默认：zh_cn，可选值：zh_cn：中文简体，en：英文，zh_en：中英文对照</td>
</tr>
<tr>
<td>mapStyle</td>
<td>String</td>
<td>设置地图显示样式，目前支持normal（默认样式）、dark（深色样式）、light（浅色样式）、fresh(osm清新风格样式)四种</td>
</tr>
</tbody>
</table>
<h1>AmapManager</h1>
<p>方法</p>
<table>
<thead>
<tr>
<th>名称</th>
<th>返回类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>getMap</td>
<td>AMap.Map</td>
<td>返回地图实例，注入该管理实例的组件的地图实例</td>
</tr>
<tr>
<td>getComponent(vid:String)</td>
<td>AMap[component]</td>
<td>返回 vid 对应的组件实例</td>
</tr>
</tbody>
</table>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件</th>
<th>参数</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>complete</td>
<td></td>
<td>地图图块加载完成后触发事件</td>
</tr>
<tr>
<td>click</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标左键单击事件 相关示例</td>
</tr>
<tr>
<td>dblclick</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标左键双击事件</td>
</tr>
<tr>
<td>mapmove</td>
<td></td>
<td>地图平移时触发事件</td>
</tr>
<tr>
<td>hotspotclick</td>
<td>{type,lnglat,name,id}</td>
<td>鼠标点击热点时触发（自v1.3 新增）</td>
</tr>
<tr>
<td>hotspotover</td>
<td>{type,lnglat,name,id}</td>
<td>鼠标滑过热点时触发（自v1.3 新增）</td>
</tr>
<tr>
<td>hotspotout</td>
<td>{type,lnglat,name,id}</td>
<td>鼠标移出热点时触发（自v1.3 新增）</td>
</tr>
<tr>
<td>movestart</td>
<td></td>
<td>地图平移开始时触发</td>
</tr>
<tr>
<td>moveend</td>
<td></td>
<td>地图平移结束后触发。如地图有拖拽缓动效果，则在缓动结束后触发</td>
</tr>
<tr>
<td>zoomchange</td>
<td></td>
<td>地图缩放级别更改后触发</td>
</tr>
<tr>
<td>zoomstart</td>
<td></td>
<td>缩放开始时触发</td>
</tr>
<tr>
<td>zoomend</td>
<td></td>
<td>缩放停止时触发</td>
</tr>
<tr>
<td>mousemove</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标在地图上移动时触发</td>
</tr>
<tr>
<td>mousewheel</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标滚轮开始缩放地图时触发</td>
</tr>
<tr>
<td>mouseover</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标移入地图容器内时触发</td>
</tr>
<tr>
<td>mouseout</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标移出地图容器时触发</td>
</tr>
<tr>
<td>mouseup</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标在地图上单击抬起时触发</td>
</tr>
<tr>
<td>mousedown</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标在地图上单击按下时触发</td>
</tr>
<tr>
<td>rightclick</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标右键单击事件</td>
</tr>
<tr>
<td>dragstart</td>
<td></td>
<td>开始拖拽地图时触发</td>
</tr>
<tr>
<td>dragging</td>
<td></td>
<td>拖拽地图过程中触发</td>
</tr>
<tr>
<td>dragend</td>
<td></td>
<td>停止拖拽地图时触发。如地图有拖拽缓动效果，则在拽停止，缓动开始前触发</td>
</tr>
<tr>
<td>resize</td>
<td></td>
<td>地图容器大小改变事件</td>
</tr>
<tr>
<td>touchstart</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>触摸开始时触发事件，仅适用移动设备</td>
</tr>
<tr>
<td>touchmove</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>触摸移动进行中时触发事件，仅适用移动设备</td>
</tr>
<tr>
<td>touchend</td>
<td></td>
<td></td>
</tr>
</tbody>
</table>
</section></template>

<script>
import Demo from 'demos/amap.vue';
export default {
  components: {
    Demo
  }
}
</script>


