<p><demo></demo></p>

<h2>静态属性</h2>
<p>仅且可以初始化配置，不支持响应式。</p>
<table>
<thead>
<tr>
<th>名称</th>
<th>类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>clickable</td>
<td>Boolean</td>
<td>图层是否可点击，若为可点击则GroundImage支持鼠标点击事件默认值：false</td>
</tr>
<tr>
<td>bounds</td>
<td>Array</td>
<td>区域</td>
</tr>
<tr>
<td>url</td>
<td>String</td>
<td>图片路径</td>
</tr>
</tbody>
</table>
<h2>动态属性</h2>
<p>支持响应式。</p>
<table>
<thead>
<tr>
<th>名称</th>
<th>类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>path</td>
<td>Array</td>
<td>多边形轮廓线的节点坐标数组，当为“环”多边形时（多边形区域在多边形内显示为“岛”），path为二维数组，数组元素为多边形轮廓的节点坐标数组“环”多边形时，要求数组第一个元素为外多边形，其余为“岛”多边形，外多边形需包含“岛”多边形，否则程序不作处理</td>
</tr>
<tr>
<td>opacity</td>
<td>Number</td>
<td>图片透明度，取值范围[0,1]，0表示完全透明，1表示不透明默认值：1</td>
</tr>
</tbody>
</table>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件</th>
<th>参数</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>click</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标左键单击事件</td>
</tr>
<tr>
<td>dblclick</td>
<td><a href="http://lbs.amap.com/api/javascript-api/reference/event/#MapsEvent">MapsEvent</a></td>
<td>鼠标左键双击事件</td>
</tr>
</tbody>
</table>
</section></template>

