<template><section><h1>Search-Box</h1>
<hr>
<h2>API</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
</tr>
</thead>
<tbody>
<tr>
<td>searchOption</td>
<td>搜索条件</td>
<td>Object</td>
</tr>
<tr>
<td>onSearchResult</td>
<td>搜索回调函数</td>
<td>function</td>
</tr>
</tbody>
</table>
<h3>searchOption</h3>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>city</td>
<td>城市名</td>
<td>String</td>
<td>全国</td>
</tr>
<tr>
<td>citylimit</td>
<td>是否限制城市内搜索</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>onSearchResult</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
</tr>
</thead>
<tbody>
<tr>
<td>pois</td>
<td>经纬度对象数组</td>
<td>Object</td>
</tr>
</tbody>
</table>
<h2>使用说明</h2>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">el-amap-search-box</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"search-box"</span> <span class="hljs-attr">:search-option</span>=<span class="hljs-string">"searchOption"</span> <span class="hljs-attr">:on-search-result</span>=<span class="hljs-string">"onSearchResult"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-amap-search-box</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">el-amap</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">el-amap-marker</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"marker in markers"</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"marker"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-amap-marker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">el-amap</span>&gt;</span>
</code></pre>
<pre><code class="language-javascript">onSearchResult(pois) {
  <span class="hljs-keyword">let</span> latSum = <span class="hljs-number">0</span>;
  <span class="hljs-keyword">let</span> lngSum = <span class="hljs-number">0</span>;
  pois.forEach(<span class="hljs-function"><span class="hljs-params">poi</span> =&gt;</span> {
    <span class="hljs-keyword">let</span> {lng, lat} = poi;
    lngSum += lng;
    latSum += lat;
    <span class="hljs-keyword">this</span>.markers.push([poi.lng, poi.lat]);
  });
  <span class="hljs-keyword">let</span> center = {
    <span class="hljs-attr">lng</span>: lngSum / pois.length,
    <span class="hljs-attr">lat</span>: latSum / pois.length
  };
  <span class="hljs-keyword">this</span>.amapManager.getMap().setZoomAndCenter(<span class="hljs-number">17</span>, <span class="hljs-keyword">new</span> AMap.LngLat(center.lng, center.lat));
}
</code></pre>
<p><searchbox></searchbox></p>

</section></template>

<script>
import searchbox from 'demos/searchbox.vue';
export default {
  components: {
    searchbox
  }
}
</script>


